import { useState, useEffect, useRef } from 'react';
import { useHistory } from 'react-router';
import { getDocument, publishDocument } from '../../scripts/connect-firebase.js';
import { INTV_META_DATA } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData';

const COLLECTION_ID = 'scenario-required-actions';

const ActionAssociations = (props) => {
    const { formId } = props.match.params;
    const [mdbForm, setMDBForm] = useState(null);
    const [prompts, setPrompts] = useState();
    const associationsRef = useRef();
    let history = useHistory();

    let actions = mdbForm?.criticalActions;

    // Load in form data
    useEffect(() => {
        if (formId) {
            getDocument(COLLECTION_ID, formId)
                .then(res => setMDBForm(res))
                .catch(err => console.error(err))
        } else throw Error ('Unable to fetch debriefer info');
    }, [formId]);

    // Set list of prompts
    useEffect(() => {
        // Expensive. useEffect ensures no re-searching prompts.
        actions &&
        setPrompts( getMatchingPrompts(actions) );
    }, [actions]);

    const handleSubmit = () => {
        let obj = {};
        for (let item of associationsRef.current)
        {
            if (item.tagName === 'SELECT')
            {
                obj[item.name] = item.value;
            }
            else
            {
                if (!obj[item.name]) obj[item.name] = [];
                if (item.checked)
                    obj[item.name].push(item.value);
            }
        }
        
        mdbForm.associations = obj;
        
        publishDocument(COLLECTION_ID, mdbForm.scenarioName, mdbForm);
        history.push('/debriefer-entry-list/');
        alert('Prompt assocations uploaded.');
    }

    const getSelected = (parent) => {
        return mdbForm.associations 
            ? mdbForm.associations[parent]
            : null;
    }
    const isChecked = (parent, item) => {
        return mdbForm.associations 
            ? mdbForm.associations[parent]?.includes(item) 
            : false;
    }

    return (
        <div id="associations-wrapper">
            <h1 id="title">Debriefer Prompt Associations</h1>
            <p className="hint-text">To be used to associate the correct action(s) with their prompts.</p>
            {
                mdbForm
                ? <form id="associations-list" ref={associationsRef} >
                    {prompts && prompts.map(e => (
                        e.selectionType === "one" 
                        ? // OR
                        <label key={e.id} className="associations-or">
                            {e.label}
                            {
                            <select name={e.id} defaultValue={getSelected(e.id)}>
                                {
                                e.children.map(child => (
                                    <option key={child.id} value={child.id}>{child.label}</option>
                                ))
                                }
                            </select>
                            }
                        </label>
                        : // AND
                        <div key={e.id} className="associations-and">
                            {e.label}
                            {
                            e.children.map(child => (
                                <label key={child.id}>
                                    {child.label}
                                    <input type="checkbox" value={child.id} name={e.id} defaultChecked={isChecked(e.id, child.id)}/>
                                </label>
                            ))
                            }
                        </div>
                    ))}
                </form>
                : <p>Loading...</p>
            }
            {
                prompts && prompts.length === 0 &&
                <p>There are no prompts to associate.</p>
            }
            <button type="button" onClick={handleSubmit} id="associations-submit">Submit</button>
        </div>
    );
}

const getAllPrompts = () => {
    let propsObj = {};

    for(let item of INTV_META_DATA)
    {
        if (item.type === 'prompt')
        {
            item.children = [];
            propsObj[item.id] = item;
        }
        else if (item.type === 'intervention' || item.type === 'answer')
        {
            if (item.prompt)
                propsObj[item.prompt].children.push(item);
        }
    }

    return propsObj;
}

const getMatchingPrompts = (list) => {
    if (!list) return [];

    const allPrompts = getAllPrompts();
    let flattenedIds = [];
    let matchingPrompts = [];
    
    // Flatten Ids
    for(const block in list) {
        for (const item in list[block]){
            let curr = list[block][item];
            if (Array.isArray(curr)) {
                for (let id of curr) {
                    if (id !== "" && !flattenedIds.includes(id))
                        flattenedIds.push(id);
                }
            }
        }
    }
    
    // Get prompts
    for (let prompt in allPrompts) {
        for (let child of allPrompts[prompt].children) {
            if (flattenedIds.includes(child.id) && !matchingPrompts.includes(allPrompts[prompt])) {
                matchingPrompts.push(allPrompts[prompt])
            }
        }
    }

    return matchingPrompts;
}

export default ActionAssociations;